import React from 'react';

class Homepage extends React.Component {
  // define initial states and bind functions
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      success: '',
      emailValue: '',
      response: '',
      status: ''
    }
    this.onTextboxChange = this.onTextboxChange.bind(this);
    this.subscribe = this.subscribe.bind(this);
  }
  onTextboxChange(event) {
    this.setState({
      emailValue: event.target.value
    });
  }
  subscribe(event) {

    this.setState({
      isLoading: true
    });

    const {
      emailValue
    } = this.state;

    if(emailValue) {
      fetch('/subscribe')
        .then(res => res.json())
        .then(json => {
          if(json.success) {
            this.setState({
              response: json.message,
              isLoading: false
            });
          } else {
            this.setState({
              response: json.message,
              isLoading: false
            });
          }
        });
    } else {
      this.setState({
        status: 'error',
        response: 'Please enter a valid email address.',
        isLoading: false
      });
    }
  }
  render() {
    const {
      isLoading,
      emailValue,
      response,
      status
    } = this.state;
    return (
      <div>
        <header>
          <div className="logo"></div>
          <h3>
            In case you havenâ€™t, is a grassroots initiative 
            combating the extortion efforts of major record 
            labels on musical talent.
          </h3>
          <h2>Get Invited</h2>
          <p>
            Heard offers a $99/month subscription 
            service allowing artists to download 
            unlimited instrumentals, while automatically 
            redistributing monthly earnings back to 
            the producers on a per download basis.
          </p>
          <input type="email" placeholder="email@address.." value={emailValue
          } onChange={this.onTextboxChange}/>
          <button onClick={this.subscribe}>
            {
              (isLoading) ? (
                <span>...</span>
              ) : (
                <span>Subscribe</span>
              )
            }
          </button>
          {
            (response) ? (
              <p className={status}>{response}</p>
            ) : (null)
          }
        </header>
      </div>
    );
  }
}

export default Homepage;
